
.inner {
    padding: 0 30px;
}
img {
    width: 100%;
}
.line {
    border: 2px solid #6F3B22;
    width: 100%;
    display: block;
}


.image {
    flex: 1 1 25%;
    border: 2px solid #719F9D;
    border-radius: 20px;
    padding: 15px;
    background: rgba(255,255,255,50%);
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
}
.image img {
    max-width: 100%;
    object-fit: cover;
}
.card {
    flex: 1 1 75%;
    color: #6F3B22;
}
.card h2 {
    font-size: 2rem;
    margin-bottom: 10px;

}
.text {
    display: flex;
    flex-direction: column;

}
.text span {
    font-size: 1.5rem;
    line-height: 2rem;
}
